import React, { useCallback, useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack, Button, Switch, Select
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import Icon from '../../../../components/Icon';
import style from './PersonDetails.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import MiniBox from '../../../../layout/MiniBox';
import SkeletonLoader from '../../../../components/SkeletonLoader';
import { callParentMethod, sendMessage } from '../../../../utils/sdk';
import { useModuleData, useModulePageData } from '../../../../hooks/module';
import { Data } from '../../../../modules/WalletList';
import { getAccountDetails } from '../../../../store/middlewares/account';
import { COMMON } from '../../../../const/messages';
import { PersonDetailsData } from '../../../../modules/WalletList/PersonDetails';
import Map from '../../../../components/Map';
import { fetchLocaleConfig } from '../../../../store/middlewares/core';
// import { addResource } from '../../../../locale';
import { updateSelectedLang } from '../../../../store/actionCreators/core';

const PersonDetails = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();
  const person = useAppSelector((store) => store.user.personDetails, shallowEqual);
  const { isDarkMode } = useAppSelector((store) => store.core.config, shallowEqual);
  const selectedLang = useAppSelector((store) => store.core.selectedLang, shallowEqual);
  const programConfig = useAppSelector((store) => store.core.programConfig, shallowEqual);

  const { startScreen = 'accountList' } = useModuleData<Data>();
  const { showBack, showLogout } = useModulePageData<PersonDetailsData, Data['startScreen']>('personDetails');
  const { id } = useParams<{id: string}>();

  const { gmapKey } = useModuleData<Data>();

  const addressString = useMemo(() => {
    if (person) {
      const { address } = person;
      return address ? `${address.line1},${address.line2},${address.city},${address.country}` : '';
    }
    return '';
  }, [person]);

  const sdkLocales: Array<{fileUrl: string, language: string}> = useMemo(() => programConfig?.brand?.sdkLocale.data || [], []);

  const onChangeLang = useCallback((lang: string) => {
    dispatch(updateSelectedLang(lang));
    dispatch(fetchLocaleConfig(lang))
      .then((json) => {
        // addResource(lang, json);
        callParentMethod('changeLocale', [lang, json]);
      });
  }, []);

  useEffect(() => {
    if (id) {
      dispatch(getAccountDetails(id));
    }
  }, [id]);

  return (
    <div className="page">
      {/* (Update loader) */}
      {!person ? (
        <SkeletonLoader name="AccountDetails" />
      ) : (
        <MiniBox
          showBack={showBack}
          onBack={() => {
            sendMessage(COMMON.BACK_PRESSED, {
              from: 'accountList'
            });
            if (startScreen !== 'accountDetails') {
              navigate(-1);
            }
          }}
          heading={t('person_profile.title')}
          footer={showLogout ? (
            <Button
              data-testid="logout-btn"
              onClick={() => {
                sendMessage(COMMON.LOGOUT);
              }}
              width="100%"
              variant="primary"
            >
              {t('person_profile.logout')}
            </Button>
          ) : undefined}
        >
          <Box width="100%" paddingTop="15px">

            <Box className={style.otherDetails}>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" textAlign="left" className={`${style.otherDetailsInfoLabel} text-light`}>{t('person_profile.name')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue}>{person?.firstName}</Text>
              </Box>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" textAlign="left" className={`${style.otherDetailsInfoLabel} text-light`}>{t('person_profile.mobile')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue}>{person?.phone}</Text>
              </Box>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" textAlign="left" className={`${style.otherDetailsInfoLabel} text-light`}>{t('person_profile.email')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue}>{person?.email}</Text>
              </Box>
            </Box>

            <Text variant="large" mb="16px">{t('person_profile.actions')}</Text>
            <Box>
              <Box>
                <HStack className={`${style.actionBox} ${style.actionBoxDarkMode}`}>
                  <Text variant="regular">{t('person_profile.darkMode')}</Text>
                  <Switch
                    variant="primary"
                    isChecked={isDarkMode}
                    onChange={() => {
                      sendMessage(COMMON.TOGGLE_DARKMODE);
                    }}
                  />
                </HStack>
              </Box>
              <Box>
                <HStack className={style.actionBox}>
                  <Text variant="regular">Language</Text>
                  <div>
                    <Select
                      value={selectedLang}
                      onChange={(e) => {
                        onChangeLang(e.target.value);
                      }}
                    >
                      <option value="en">EN</option>
                      {sdkLocales.map((l) => <option key={`lang-${l.language}`} value={l.language}>{l.language.toUpperCase()}</option>)}
                    </Select>
                  </div>
                </HStack>
              </Box>
              <Box onClick={() => {
                sendMessage(COMMON.HELP_PRESSED, {
                  from: 'personDetails'
                });
              }}
              >
                <HStack className={style.actionBox}>
                  <Text variant="regular">{t('person_profile.help')}</Text>
                  <Icon icon="Arrow-small-right" size={12} />
                </HStack>
              </Box>

              <Box
                role="button"
                onClick={() => {
                  sendMessage(COMMON.GET_IN_TOUCH_PRESSED, {
                    from: 'personDetails'
                  });
                }}
              >
                <HStack className={style.actionBox}>
                  <Text variant="regular">{t('person_profile.getInTouch')}</Text>
                  <Icon icon="Arrow-small-right" size={12} />
                </HStack>
              </Box>

              <Box
                role="button"
                onClick={() => {
                  sendMessage(COMMON.DISCLOSURES_PRESSED, {
                    from: 'personDetails'
                  });
                }}
              >
                <HStack className={style.actionBox}>
                  <Text variant="regular">{t('person_profile.disclosures')}</Text>
                  <Icon icon="Arrow-small-right" size={12} />
                </HStack>
              </Box>
            </Box>

            <Text variant="large" mb="16px" mt="20px">{t('person_profile.location')}</Text>
            <Box className={style.otherDetails}>
              <Box className={style.location__map}>
                <Map zoom={7} apiKey={gmapKey || ''} address={addressString} />
              </Box>
              <Box className={style.otherDetailsInfo1} borderBottom="1px solid var(--border-color-1)">
                <Text variant="regular" textAlign="left" className="text-light">{t('person_profile.address')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue1}>
                  {addressString}
                </Text>
              </Box>
              <Box className={style.otherDetailsInfo1}>
                <Text variant="regular" textAlign="left" className="text-light">{t('person_profile.country')}</Text>
                <Text variant="regular" textAlign="left" className={style.otherDetailsInfoValue1}>{person.address?.country}</Text>
              </Box>
            </Box>
          </Box>
        </MiniBox>
      )}

    </div>

  );
};

export default PersonDetails;
